div.scoreflash {
  position: absolute;
  width: 30vw;
  height: 20vh;
  z-index: 1000;
  margin-left: 38vw; 
  margin-top: 25vh;
  border-radius: 40%;
  border: 5px solid var(--highlight);
  
  -webkit-box-shadow:
      0 0 60px 30px var(--primary), 
      0 0 100px 60px var(--secondary), 
      0 0 160px 90px var(--tertiary) ,
      0 0 240px 120px transparent;
  
          box-shadow:
      0 0 60px 30px var(--primary), 
      0 0 100px 60px var(--secondary), 
      0 0 160px 90px var(--tertiary) ,
      0 0 240px 120px transparent;
      background-color: var(--tertiary); 
      background-size: 200% 200%;
      background-image: -webkit-gradient(linear, left top, left bottom, from(var(--secondary)), to(var(--tertiary)));
      background-image: -webkit-linear-gradient(top, var(--secondary),var(--tertiary));
      background-image: -o-linear-gradient(top,  var(--secondary),var(--tertiary));
      background-image: -webkit-gradient(linear,  left top, left bottom,  from(var(--secondary)),to(var(--tertiary)));
      background-image: linear-gradient(to bottom,  var(--secondary),var(--tertiary));
      filter:progid:DXImageTransform.Microsoft.gradient(GradientType=0,startColorstr=var(--secondary), endColorstr=var(--tertiary));
     -webkit-animation: Button-Gradient 0.44s ease infinite;
     animation: Button-Gradient 0.44s ease infinite;
}
